const handleExport = async () => {
  const workbook = new ExcelJS.Workbook();
  const sheet = workbook.addWorksheet("Housekeeping Audit", {
    pageSetup: { paperSize: 9, orientation: "portrait" },
  });

  // ======== PAGE TITLE ========
  sheet.mergeCells("A1:F1");
  const title = sheet.getCell("A1");
  title.value = "HOUSEKEEPING AUDIT CHECKLIST";
  title.font = { size: 18, bold: true };
  title.alignment = { horizontal: "center" };

  sheet.addRow([]);
  sheet.addRow([]);

  // ======== BASIC DETAILS ========
  const infoRows = [
    ["Base Location :", "" , "", "Inspected by:", ""],
    ["Ward/Unit/Department:", "", "", "Date:", new Date().toLocaleDateString()],
    ["", "", "", "Time:", new Date().toTimeString().slice(0,5)]
  ];

  infoRows.forEach((row) => sheet.addRow(row));

  sheet.addRow([]);
  sheet.addRow([]);

  // ======== MEASURABLE STANDARD ========
  sheet.mergeCells(`A8:F10`);
  const std = sheet.getCell("A8");

  std.value =
    `Measurable Standard: Clean = dust free, tidy & spotless.\n` +
    `KEY: (‚úì) Met    (‚úó) Not Met    (N/A) Not Applicable`;

  std.font = { size: 10, italic: true };
  std.alignment = { wrapText: true };

  sheet.addRow([]);
  sheet.addRow([]);

  // ======== TABLE HEADER (Same as PDF) ========
  const headerRow = sheet.addRow([
    "Elements",
    "Comments",
    "Out of",
    "Score",
    "Action timeframe",
    "Action Taken Y/N",
  ]);

  headerRow.eachCell((cell) => {
    cell.font = { bold: true };
    cell.alignment = { horizontal: "center", vertical: "middle" };
    cell.border = {
      top: { style: "thin" },
      left: { style: "thin" },
      right: { style: "thin" },
      bottom: { style: "thin" },
    };
  });

  sheet.getRow(sheet.lastRow.number).height = 22;

  // ======== CATEGORY + ITEMS (Matches PDF Format) ========
  filteredCategories.forEach((category, index) => {
    // Category Header Row
    const catRow = sheet.addRow([`${index + 1} ${category.name.toUpperCase()}`]);
    sheet.mergeCells(`A${catRow.number}:F${catRow.number}`);

    catRow.font = { bold: true, size: 12 };
    catRow.alignment = { horizontal: "left" };
    catRow.fill = {
      type: "pattern",
      pattern: "solid",
      fgColor: { argb: "FFE6E6E6" }, // light gray like pdf
    };

    sheet.addRow([]);

    // Items under category
    category.items.forEach((item) => {
      const row = sheet.addRow([
        item.name || "",
        item.comments || "",
        item.outOf || "",
        item.score || "",
        item.frame || "",
        item.status === "checked"
          ? "Y"
          : item.status === "crossed"
          ? "N"
          : "N/A",
      ]);

      row.height = 20;

      row.eachCell((cell) => {
        cell.border = {
          top: { style: "thin" },
          left: { style: "thin" },
          right: { style: "thin" },
          bottom: { style: "thin" },
        };
        cell.alignment = { wrapText: true, vertical: "middle" };
      });
    });

    sheet.addRow([]);
  });

  // ===== SUMMARY =====
  const allItems = filteredCategories.flatMap((c) => c.items);
  const checked = allItems.filter((i) => i.status === "checked").length;

  const summaryRow = sheet.addRow([
    `SCORED: ${checked} / ${allItems.length}  (${Math.round(
      (checked / allItems.length) * 100
    )}%)`,
  ]);

  sheet.mergeCells(`A${summaryRow.number}:F${summaryRow.number}`);
  summaryRow.font = { bold: true, size: 14 };
  summaryRow.alignment = { horizontal: "center" };

  // ===== DOWNLOAD =====
  const buffer = await workbook.xlsx.writeBuffer();
  const blob = new Blob([buffer], {
    type: "application/vnd.openxmlformats-officedocument.spreadsheetml.sheet",
  });

  const url = URL.createObjectURL(blob);
  const a = document.createElement("a");
  a.href = url;
  a.download = `Housekeeping_Audit_${Date.now()}.xlsx`;
  a.click();
  URL.revokeObjectURL(url);
};
